.sign {
  min-width: 480px;
  min-height: 430px;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.registration {
  display: flex;
}
.button {
  margin: 0;
  padding: 0;
}
.form {
  text-align: center;
}
